<template>
  <div class="content-div">
    <div class="container">
      <div class="row">
        <div class="col-12 col-md-8 my-3">
          <span class="h1 mr-2">COVID-19 Global Diagnosis</span>
        </div>
        <div class="col-12 col-md-4 text-right my-3 updateTime">
          Update : {{allData?allData.Date:'-'}} 
        </div>
        <div class="col-12 col-sm-12 col-md-6 col-lg-4 mb-4 move">
          <div class="infoBox" style="border-color:#ffc107">
            <div class="title text-warning">Coronavirus</div>
            <div class="number text-right">{{allData?toCurrency(allData.TotalConfirmed):"0"}}</div> 
            <div class="h6 text-warning text-right"><br> +{{allData?toCurrency(allData.NewConfirmed):"0"}} </div>
          </div>
        </div>
        <div class="col-12 col-sm-6 col-md-6 col-lg-4 mb-4 move">
          <div class="infoBox" style="border-color:#dc3545">
            <div class="title text-danger">deaths</div>
            <div class="number text-right">{{allData?toCurrency(allData.TotalDeaths):"0"}}</div>
            <div class="h6 text-danger text-right"><br> +{{allData?toCurrency(allData.NewDeaths):"0"}}</div>
          </div>
        </div>
        <div class="col-12 col-sm-6 col-md-6 col-lg-4 mb-4 move">
          <div class="infoBox" style="border-color:#40b883">
            <div class="title ">recovered</div>
            <div class="number text-right">{{allData?toCurrency(allData.TotalRecovered):"0"}}</div>
            <div class="h6 text-success text-right"><br> +{{allData?toCurrency(allData.NewRecovered):"0"}} </div>
          </div>
        </div>
      </div>
      <div class="row">
        <div class="col-12 col-md-6">
          <div class="row">
            <div class="col-12 col-sm-12 col-md-12 col-lg-12 mb-4 move">
              <div class="infoBox border-light" >
                <div class="title text-light countyTitle mb-3">ALL</div>
                <div class="row">
                  <div class="col-12">
                    <canvas id="chartAll"></canvas>
                  </div>
                </div>
              </div>  
            </div>
            <div class="col-12 col-sm-12 col-md-12 col-lg-12 mb-4 move">
              <div class="infoBox border-light" >
                <div class="title text-light countyTitle mb-3">{{countryData.country}}</div>
                <div class="row">
                  <div class="col-12 col-lg-12 my-4">
                    <div class="number text-warning" style="font-size: 50px">
                      {{toCurrency(countryData.cases)}}
                      <span class="text-info" style="font-size:20px">+{{toCurrency(countryData.todayCases)}} (New Cases)</span>
                    </div>
                  </div>
                  <div class="col-12 col-sm-6 col-md-6 mb-4">
                    <div class="subInfoBox border-danger">
                      <div class="title text-danger">deaths</div>
                      <div class="number text-right">{{toCurrency(countryData.deaths)}}</div>
                    </div>
                  </div>
                  <div class="col-12 col-sm-6 col-md-6 mb-4">
                    <div class="subInfoBox">
                      <div class="title">recovered</div>
                      <div class="number text-right">{{toCurrency(countryData.recovered)}}</div>
                    </div>
                  </div>
                  <div class="col-12">
                    <canvas id="chartCountryHistory" width="100%" height="100%" ></canvas>
                  </div>
                </div>
              </div>  
            </div>
            <div class="col-12 col-sm-12 col-md-12 col-lg-12 mb-4 move">
              <div class="infoBox border-light" >
                <div class="title text-light countyTitle mb-3">Top 10</div>
                <div class="row">
                  <div class="col-12">
                    <canvas id="chartCountries" width="100%" height="100%" ></canvas>
                  </div>
                </div>
              </div>  
            </div>
          </div>
        </div>
        <div class="col-12 col-md-6">
          <div class="row">
            <div class="col-12 col-sm-12 col-md-12 col-lg-12 mb-4 move">
              <div class="infoBox border-info" >
                <div class="title text-info">Countries <small style="font-size: 10px" class="float-right text-light">Click to change country</small></div>
                <div class="table-responsive">
                  <table class="table table-sm table-hover table-dark">
                    <thead>
                      <tr>
                        <th @click="changSort(1)">Country</th>
                        <th @click="changSort(2)" class="text-right">Cases</th>
                        <th @click="changSort(3)" class="text-right">Deaths</th>
                        <th @click="changSort(4)" class="text-right">Recovered</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr v-for="(key,index) in countriesData" :key="index" @click="changCountry(index)">
                      <!-- <tr v-for="(key,index) in countriesData" :key="index"> -->
                        <td>{{key.Country}}</td>
                        <td align="right">{{toCurrency(key.TotalConfirmed)}}</td>
                        <td align="right">{{toCurrency(key.TotalDeaths)}}</td>
                        <td align="right">{{toCurrency(key.TotalRecovered)}}</td>
                      </tr>
                    </tbody>  
                  </table>
                </div>
              </div>  
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Chart from 'chart.js';

export default {
  props: {
    
  },
  components: {

  },
  mounted:function(){
    this.getAllData();
  },
  data(){
    return { 
      allData : null,
      countriesData : null,
      countryData : {
        country:  "taiwan",
        countryId: "taiwan",
        cases: 0,
        deaths: 0,
        recovered: 0,
        todayCases: 0
      },
      history:"",
      countryHistory:"",
      histortyChart : null,
      chartAll : null,
      chartCountries: null,
    }
  },
  methods: {
    changSort(id){
      if(id == 1){
        this.countriesData.sort(function(a, b){
          return a.CountryCode - b.CountryCode;
        });
      }
      if(id == 2){
        this.countriesData.sort(function(a, b){
          return b.TotalConfirmed - a.TotalConfirmed;
        });
      }
      if(id == 3){
        this.countriesData.sort(function(a, b){
          return b.TotalDeaths - a.TotalDeaths;
        });
      }
      if(id == 4){
        this.countriesData.sort(function(a, b){
          return b.TotalRecovered - a.TotalRecovered;
        });
      }
    },
    getAllData(){
      this.axios.get('https://api.covid19api.com/summary')
      .then(response=>{
        this.countriesData = response.data.Countries.sort(function(a, b){
          return b.TotalConfirmed - a.TotalConfirmed;
        });

        this.allData = response.data.Global;
        
       
        this.setChartAll(response.data.Global.TotalConfirmed,response.data.Global.TotalDeaths,response.data.Global.TotalRecovered);
        
        this.setChartCountries();
        this.getCountryData(this.countryData.countryId);
        this.getCountryHistory(this.countryData.countryId);
        
      })
      .catch(function (error) {
        console.log(error);
      });
    },
    getCountriesData(){
      this.axios.get('https://api.covid19api.com/summary')
      .then(response=>{
        console.log(response.data);
        this.countriesData = response.data.Countries;

      })
      .catch(function (error) {
        console.log(error);
      });
    },
    getCountryData(country){
      for(var key in this.countriesData){
        if(this.countriesData[key].Slug == country){
          this.countryData = {
            country:  this.countriesData[key].Country,
            countryId: this.countriesData[key].Slug,
            cases: this.countriesData[key].TotalConfirmed,
            deaths: this.countriesData[key].TotalDeaths,
            recovered: this.countriesData[key].TotalRecovered,
            todayCases: this.countriesData[key].NewConfirmed
          }
        }
      }
    },
    getHistory(){
      this.axios.get('https://api.covid19api.com/premium/country/data/'+this.countryData.country)
      .then(response=>{
        console.log(response.data);
      })
      .catch(function (error) {
        console.log(error);
      });
    },
    getCountryHistory(country){
      this.axios.get('https://api.covid19api.com/live/country/'+country+'')
      .then(response=>{
        this.history = response.data;
        this.setChartCountryHistory();
      })
      .catch(function (error) {
        console.log(error);
      });
    },
    changCountry(key){
        this.countryData = {
          country:  this.countriesData[key].Country,
          countryId: this.countriesData[key].Slug,
          cases: this.countriesData[key].TotalConfirmed,
          deaths: this.countriesData[key].TotalDeaths,
          recovered: this.countriesData[key].TotalRecovered,
          todayCases: this.countriesData[key].NewConfirmed
        };
        this.histortyChart.destroy();
        this.getCountryHistory(this.countriesData[key].Slug);
    },
    changeTime(time){
      var newTime = new Date(time);
      this.allData.updated = newTime;
    },
    toCurrency(num){
      var parts = num.toString().split('.');
      parts[0] = parts[0].replace(/\B(?=(\d{3})+(?!\d))/g, ',');
      return parts.join('.');
    },
    setChartAll(a,b,c){
      console.log(a,b,c);
      var chartAll = document.getElementById('chartAll');

      this.chartAll  = new Chart(chartAll, {
          type: 'doughnut',
          data: {
              labels: [ 'CORONAVIRUS', 'DEATHS','RECOVERED'],
              datasets: [
              {
                  label: '# of Votes',
                  data: [a, b,  c],
                  backgroundColor: [
                      'rgba(255, 206, 86, 0.2)',
                      'rgba(255, 99, 132, 0.2)',
                      'rgba(75, 192, 192, 0.2)'
                  ],
                  borderColor: [
                      'rgba(255, 206, 86, 1)',
                      'rgba(255, 99, 132, 1)',
                      'rgba(75, 192, 192, 1)'
                  ],
                  borderWidth: 1
              }]
          },
          options: {
              scales: {
                  yAxes: [{
                      ticks: {
                          beginAtZero: true
                      }
                  }]
              }
          }
      });
    },
    setChartCountries(){
      var chartCountries = document.getElementById('chartCountries');
      var _countriesData =  this.countriesData;
      this.chartCountries = new Chart(chartCountries, {
          type: 'horizontalBar',
          // type: 'bar',
          data: {
              labels: [
                _countriesData[0].Country,
                _countriesData[1].Country,
                _countriesData[2].Country,
                _countriesData[3].Country,
                _countriesData[4].Country,
                _countriesData[5].Country,
                _countriesData[6].Country,
                _countriesData[7].Country,
                _countriesData[8].Country,
                _countriesData[9].Country
              ],
              datasets: [{
                  label: ['Top 10'],
                  data: [
                    _countriesData[0].TotalConfirmed,
                    _countriesData[1].TotalConfirmed,
                    _countriesData[2].TotalConfirmed,
                    _countriesData[3].TotalConfirmed,
                    _countriesData[4].TotalConfirmed,
                    _countriesData[5].TotalConfirmed,
                    _countriesData[6].TotalConfirmed,
                    _countriesData[7].TotalConfirmed,
                    _countriesData[8].TotalConfirmed,
                    _countriesData[9].TotalConfirmed
                  ],
                  backgroundColor: 'rgba(255, 255, 255, 0.2)',
                  borderColor: 'rgba(255, 255, 255, 1)',
                  borderWidth: 1
              }]
          },
          options: {
              scales: {
                  yAxes: [{
                      ticks: {
                          beginAtZero: true
                      }
                  }]
              }
          }
      });
    },
    setChartCountryHistory(){
      var chart = document.getElementById('chartCountryHistory');
      var timeLabel = [];
      var casesData = [];
      var casesData2 = [];
      var casesData3 = [];
      var data = this.history;
      for(var key in data){
        if(key == 0){
          timeLabel.push(data[key].Date);
          casesData.push(data[key].Confirmed);
          casesData2.push(data[key].Deaths);
          casesData3.push(data[key].Recovered);
        }
        if(casesData[casesData.length-1] < data[key].Confirmed){
          timeLabel.push(data[key].Date);
          casesData.push(data[key].Confirmed);
          casesData2.push(data[key].Deaths);
          casesData3.push(data[key].Recovered);
        }
      }

      this.histortyChart = new Chart(chart, {
          type: 'line',
          data: {
              labels: timeLabel,
              datasets: [
                {
                  label: 'CORONAVIRUS',
                  data: casesData,
                  backgroundColor: [
                      'rgba(255, 99, 132, 0)'
                  ],
                  borderColor: [
                      'rgba(255, 206, 86, 1)',
                  ],
                  borderWidth: 2,
                },
                {
                  label: 'DEATHS',
                  data: casesData2,
                  backgroundColor: [
                      'rgba(255, 99, 132, 0)'
                  ],
                  borderColor: [
                      'rgba(255, 99, 132, 1)',
                  ],
                  borderWidth: 2,
                },
                {
                  label: 'RECOVERED',
                  data: casesData3,
                  backgroundColor: [
                      'rgba(255, 99, 132, 0)'
                  ],
                  borderColor: [
                      'rgba(75, 192, 192, 1)'
                  ],
                  borderWidth: 2,
                },
              ]
          },
          options: {
              scales: {
                  yAxes: [{
                      ticks: {
                          beginAtZero: true
                      }
                  }]
              }
          }
      });
    }
  }
}
</script>

